import Container from '@material-ui/core/Container';
import { withStyles } from '@material-ui/core/styles';
import GoogleMapReact from 'google-map-react';
import React from 'react';

import StationView from '../containers/stationContainer';
import NewStationView from '../components/newStationView';
import { Color } from '../styles/styles';

export interface Props {
  classes: {
    mapContainer: string;
  };
  center?: {
    lat: number;
    lng: number;
  };
  zoom?: number;
  stationList: [];
}

const styles: object = (theme: any) => ({
  mapContainer: {
    padding: 0,
    height: 680,
    backgroundColor: Color.secondColor,
    boxShadow:
      '0px 2px 4px -1px rgba(0,0,0,0.2),' +
      '0px 4px 5px 0px rgba(0,0,0,0.14),' +
      '0px 1px 10px 0px rgba(0,0,0,0.12)',
  },
  [theme.breakpoints.down('lg')]: {
    mapContainer: {
      width: '100%',
    },
  },
  [theme.breakpoints.up('lg')]: {
    mapContainer: {
      width: 1200,
      margin: '0 auto',
      marginTop: '4em',
    },
  },
});

const StationListView: Function = (stationList: []) =>
  stationList.map((station: any) => {
    return (
      <StationView
        key={station.id}
        lat={station.latitude}
        lng={station.longitude}
        title={station.name}
      />
    );
  });

const MapView = ({
  classes,
  zoom = 11,
  stationList,
  center = {
    lat: 59.9133858,
    lng: 10.745384,
  },
}: Props): React.ReactElement => {
  const [newStationView, setNewStationView] = React.useState(null);
  const onMapClick = (event: any) => {
    console.log(event);
    setNewStationView(
      <NewStationView
        key="new-station"
        lat={event.lat}
        lng={event.lng}
      />
    );
  };

  return (
    <Container className={classes.mapContainer}>
      <GoogleMapReact
        bootstrapURLKeys={{ key: 'AIzaSyAHM_cgzj4zNbOg4hFs8tklaHmZlVq6g2U' }}
        defaultCenter={center}
        defaultZoom={zoom}
        onClick={onMapClick}
      >
        {StationListView(stationList)}
        {newStationView}
      </GoogleMapReact>
    </Container>
  );
};

export default withStyles(styles)(MapView);
