import { Typography } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import Popover from '@material-ui/core/Popover';
import LocationOnIcon from '@material-ui/icons/LocationOnRounded';
import GoogleMapReact from 'google-map-react';
import React, { useState } from 'react';

import { Color } from '../styles/styles';

export interface Props {
  classes: any;
  title: string;
  key: string;
  lat: number;
  lng: number;
}

const styles: object = (theme: any) => ({
  stationContainer: {
    display: 'flex',
    flexDirection: 'column',
    padding: '1em 0 1em 0',
    width: 180,
    backgroundColor: Color.secondColor,
    boxShadow:
      '0px 2px 4px -1px rgba(0,0,0,0.2),' +
      '0px 4px 5px 0px rgba(0,0,0,0.14),' +
      '0px 1px 10px 0px rgba(0,0,0,0.12)',
  },
  stationTitle: {
    margin: 4,
    textAlign: 'center',
    color: Color.gray,
    fontSize: 13.5,
  },
  stationButton: {
    margin: 12,
    backgroundColor: Color.cyan,
    '&:hover': {
      backgroundColor: Color.cyan700,
    },
  },
  stationIcon: {
    position: 'relative',
    left: '-0.5em',
    top: '-1em',
    color: Color.cyan700,
  },
});

const StationView = ({ classes, title }: Props): React.ReactElement => {
  const [anchorEl, setAnchorEl] = React.useState<SVGSVGElement | null>(null);
  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;

  const handleClick = (event: React.MouseEvent<SVGSVGElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'center',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'center',
          horizontal: 'left',
        }}
      >
        <Paper className={classes.stationContainer}>
          <Typography className={classes.stationTitle}>{title.toUpperCase()}</Typography>
          <Button variant="contained" className={classes.stationButton}>
            Data
          </Button>
          <Button variant="contained" className={classes.stationButton}>
            Enhet
          </Button>
        </Paper>
      </Popover>
      <LocationOnIcon
        fontSize={'large'}
        className={classes.stationIcon}
        onClick={handleClick}
      />
    </>
  );
};

export default withStyles(styles)(StationView);
